* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #ffffff;
}


.app .mainHeading h1 {
  text-align: center;
}

.app h1,
h2 {
  color: black;
}

.app .input {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #ccc;
}

.app .input {
  margin-top: 0.8em;
  width: 100%;
  height: 3em;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 6px;
  padding: 5px;
}

.app .input input {
  width: 100%;
  height: 100%;
  border-color: transparent;
  outline-color: transparent;
  display: inline-block;
}

.app .input:has(input:focus) {
  outline: 2px solid #000dff;
}

.app i {
  cursor: pointer;
  font-size: larger;
  font-weight: 900;
  color: grey;
  /* margin-right: 5px; */
}

.app .todos {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  /* flex-direction: column; */
}

.todo-content {
  overflow-wrap: break-word;
  word-wrap: break-word;
  white-space: normal
}

.app .todos .todo {
  margin-top: 0.8em;
  width: 100%;
  /* height          : 2em; */

  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 6px;
  padding: 5px;
  border-radius: 8px;
}

.app .todos .todo .left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.app .todos .todo .left p {
  margin-left: 3px;
  font-size: medium;
  font-weight: 700;
  font-style: normal;
  color: grey;
}

.app .add-button {
  border-radius: 6px;
}

.app .add-button:hover {
  color: #000dff;
  transform: scale(1.1);
  background-color: #f0f0f0;
}